<template>
  <div class="detailPage mt-5">
    <mavon-editor 
     class="markDwonPart"
     :value="articleDetail.technology"
     :subfield = "prop.subfield"
     :defaultOpen = "prop.defaultOpen"
     :toolbarsFlag = "prop.toolbarsFlag"
     :editable="prop.editable"
     :scrollStyle="prop.scrollStyle"/>
  </div>
</template>

<script>
export default {
  name: 'detailPage',
  data(){
    return {
      articleDetail: ''
    }
  },
  mounted () {
    var api = `https://www.domyown.top/ssm/getDetail/4`
    this.axios
      .get(api)
      .then(response => {
        this.articleDetail = response.data.extend.detailsInfo
      })
      .catch(error => (console.log(error)))
  },
  computed: {
      prop () {
        let data = {
          subfield: false,// 单双栏模式
          defaultOpen: 'preview',//edit： 默认展示编辑区域 ， preview： 默认展示预览区域 
          editable: false,
          toolbarsFlag: false,
          scrollStyle: true,
        }
        return data
      }
    }
}
</script>

<style lang="less" scoped>
.markDwonPart{
  width: 100%;
  margin: 0 auto;
  z-index: 1;
}
</style>
